.card-container {
    display: flex;
    width: 200px;
    flex-direction: column;
    background-color: #95dada;
    border: 1px solid grey;
    border-radius: 5px;
    padding: 25px;
    cursor: pointer;
    -moz-osx-font-smoothing: grayscale;
    backface-visibility: hidden;
    /* transform: translateZ(0); */
    opacity: 0.8;
    transition: opacity 0.25s ease-out;
  }
  
  .card-container:hover {
    opacity: 1;
  }